

@media only screen and (max-width: 600px) {
    #menuToggle
    {
      display: block;
      position: fixed;
      top: 50px;
      left: 50px;
      
      z-index: 10;
      
      -webkit-user-select: none;
      user-select: none;
    }
    
    #menuToggle a
    {
      text-decoration: none;      
      transition: color 0.3s ease;
    }
    
    #menuToggle input
    {
      display: block;
      width: 40px;
      height: 32px;
      position: absolute;
      top: -7px;
      left: -5px;
      
      cursor: pointer;
      
      opacity: 0; /* hide this */
      z-index: 2; /* and place it over the hamburger */
      
      -webkit-touch-callout: none;
    }
    
    #menuToggle span
    {
      display: block;
      width: 33px;
      height: 4px;
      margin-bottom: 5px;
      position: relative;
      
      background: #cdcdcd;
      border-radius: 3px;
      
      z-index: 1;
      
      transform-origin: 4px 0px;
      
      transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
                  background 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
                  opacity 0.55s ease;
    }
    
    #menuToggle span:first-child
    {
      transform-origin: 0% 0%;
    }
    
    #menuToggle span:nth-last-child(2)
    {
      transform-origin: 0% 100%;
    }
    #menuToggle input:checked ~ span
    {
      opacity: 1;
      transform: rotate(45deg) translate(-2px, -1px);
      background: #ffffff;
    }

    #menuToggle input:checked ~ span:nth-last-child(3)
    {
      opacity: 0;
      transform: rotate(0deg) scale(0.2, 0.2);
    }
    
    #menuToggle input:checked ~ span:nth-last-child(2)
    {
      transform: rotate(-45deg) translate(0, -1px);
    }

    #menu
    {
      position: relative;
      width: 100vw;
      height: 100vh;
      margin: -77px 0 0 -50px;
      padding: 50px;
      padding-top: 125px;
      
      background: #000000c2;
      list-style-type: none;
      -webkit-font-smoothing: antialiased;      
      transform-origin: 0% 0%;
      transform: translate(-100%, 0);
      
      transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0);
    }
    
    #menu ul
    {
      padding: 10px 0;
      font-size: 22px;
    }

    #menuToggle input:checked ~ ul
    {
      transform: none;
    }  

    .menuButton{
        position: relative;
        width: 100%;
        height: 100%;
    }
    li{
        box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0);
        background-color: rgba(255, 255, 255, 0);
        width: 100%;
        padding: 5px;
        height: 25%;
        margin-top: 5px;
        margin-left: 0;
    }

    li a, li a.active{
        margin-top: 20%;
        z-index: 11;
        color: white;
        font-size: 50px;
        font-weight: 600;
        text-transform: uppercase;
    }

    li a.active{
        font-size: 55px;
        color: rgb(0, 140, 255);
    }

    li:hover{
        transform: translateY(0px);
    }
    
    
    .row{
        margin: 0 auto;
    }

    .col p {
        font-size: 15px;
    }

    .col{
        margin-left: 10px;
        margin-right: 10px;
        min-width: 270px;
    }
    .boy img{
        opacity: 0;
    }

    p,h3{
        font-size: 13px;
    }

    h2{
        font-size: 20px;
    }
    .cointerner-teach{
        background-color: rgb(255, 255, 255);
        margin-top: 30px;
        padding: 20px;
        width: 100%;
        overflow:auto;
        min-height: 1000px;
        margin-bottom: 150px;
    }

    .PanelToDo{
        background-color: #e3eeff63;
        padding: 15px;
        top: -10px;
        width: 100%;
    }

    .todo{
        width: 60%;
        height: 100%;
        padding: 5px;
       }

       .doneTask{
        top: 50px;
    }

    .todobox{
        margin-top: 10px;
        margin-left: 1%;
        width: 100%;
    }
    
    .nav-main{
        height: 100px;
        left: 5vw;
        top: 20px;
    }

    .BoxPicture img{

    }
    
    .IconTech img{
        margin-top: 50px;
        margin-bottom: 40px;
        width: 70px;
        height: 80px;
    }

    .cointerner-center{
        background-image: url("../images/sky.jpg");
